* {
  box-sizing: border-box;
}

.App {
  font-family: sans-serif;
  text-align: center;
}

section {
  background: #f1f1f1;
  padding: 1rem;
  margin-bottom: 1rem;
}

label {
  display: block;
  margin-top: 1.2rem;
  margin-bottom: 0.5rem;
  font-weight: bold;
}

input {
  padding: 0.5rem;
  border-radius: 6px;
  border: "1px solid #333";
  width: 50%;
}
button {
  display: block;
  background-color: aquamarine;
  border: none;
  padding: 1rem 2rem;
  margin: 1rem auto;
}
